import React, { useEffect, useState } from 'react'
import { Redirect } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { getUserData } from 'apiServices'

const Authenticate = ({ location }) => {
    const dispatch = useDispatch()
    const user = useSelector(state => state.user)
    const status = useSelector(state => state.status)
    const [complete, setComplete] = useState(false)
    useEffect(() => {

        const email = location?.search?.slice(11)
        const get = new XMLHttpRequest()
        get.open('GET', document.location, false)
        get.send(null)
        let headers = {}
        const header = get.getAllResponseHeaders()
        let headerSplit = header.split('\r')
        for (let i = 0; i < headerSplit.length; i++) {
            let thisItem = headerSplit[i];
            let key = thisItem.substring(0, thisItem.lastIndexOf(':'));
            let value = thisItem.substring(thisItem.lastIndexOf(':') + 1);
            key = key.replace('\n', '')
            value = value.replace(' ', '')
            headers[key] = value;
        }
        const sessionId = headers.sessionid
        getUserData({ dispatch, user, status, sessionId: email ? email : sessionId, setComplete })
    }, [])
    if (user && complete) return <Redirect to='/' />
    if (!user && complete && status?.code === 401) window.location.href = 'https://dev-wms-web.sayurbox.tech/'
    if (user && complete && status?.code === 401) window.location.href = 'https://dev-wms-web.sayurbox.tech/'
    if (!user && complete && status?.code === 500) return <Redirect to='/server-error' />
    return <div className='h5'>...Authenticate</div>
}

export default Authenticate